.courses-section {
  padding-top: 60px;
  padding-bottom: 60px;

  // .courses-section__container

  &__container {}
}

.main {

  // .main__courses-section

  &__courses-section {}
}


.courses {

  // .courses__item

  &__item+&__item {
    margin-top: 50px;
  }
}

.course {
  background-color: #fff;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  grid-template-areas: "a b" "c c";
  align-items: center;

  @include m-1070 {
    grid-template-columns: 1fr 1fr;
  }

  @include m-960 {
    grid-template-areas: "a a" "b b" "c c";
  }

  // .course__image

  &__image {
    grid-area: a;

  }

  // .course__content

  &__content {

    grid-area: b;

    @include m-960 {
      padding: 4rem 0 1rem;
    }
  }

  // .course__about

  &__about {
    grid-area: c;
  }
}

.course-image {
  position: relative;

  &__circle {
    position: absolute;
    top: -2.5rem;
    right: -2rem;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 1px solid var(--blue-color);

    @include m-960 {
      right: calc(50% - 250px);
    }

    @include m-560 {
      transform: scale(0.8);
      right: -1rem;
      top: -4rem;
    }

  }

  // .course-image__tutor
  &__tutor {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    transform: translate(50%, 50%);
  }

  // .course-image__student

  &__student {
    width: 100%;

    @include m-960 {
      max-width: 400px;
      margin: 0 auto;
    }


    img {
      width: 100%;
      aspect-ratio: 2/3;
      margin: 0 auto;
      border-radius: calc(600px / 2);
      overflow: hidden;
    }
  }

  // .course-image__desc

  &__desc {
    position: absolute;
    bottom: 2rem;
    left: 0;
    width: 290px;
    min-height: 80px;
    border-radius: 40px;
    background-color: #fff;
    display: flex;
    align-items: center;
    box-shadow: 0px 20px 20px rgba(47, 65, 88, 0.3);

    @include m-960 {
      left: calc(50% - 290px);
    }

    @include m-600 {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__desc-content {
    flex: 1;
    padding: 10px;
  }

  // .course-image__icon

  &__icon {
    flex: 0 0 80px;
    width: 80px;
    position: relative;

    &--video,
    &--computer {
      &::before {
        content: '';
        display: block;
        position: relative;
        top: 0;
        left: 10px;
        width: 60px;
        height: 60px;
        background-color: var(--blue-color);
        border-radius: 50%;
      }

      &::after {
        content: '';
        display: block;
        position: absolute;
        top: 18px;
        left: 30px;
        width: 30px;
        height: 30px;
        transition: all .3s ease-in-out;
      }
    }

    &--video {
      &::after {
        background: url('../img/svg/icon-video.svg') 0 0 no-repeat;
      }
    }

    &--computer {
      &::after {
        background: url('../img/svg/icon-computer.svg') 0 0 no-repeat;
      }
    }

  }

  // .course-image__title

  &__title {
    font-size: .9rem;
    line-height: 120%;
    font-weight: 600;
    margin-bottom: 6px;
    color: var(--grey-color-10);
  }

  // .course-image__subtitle

  &__subtitle {
    font-size: .8rem;
    line-height: 120%;
    color: var(--grey-color-5);
  }
}

.course-content {
  padding: 4rem 0 4rem 4rem;
  // .course-content__title

  &__title {
    margin-bottom: 20px;

    h3,
    h2 {
      font-size: 2rem;
      line-height: 120%;
    }
  }

  // .course-content__desc

  &__desc {
    color: rgb(51, 51, 51);
    font-weight: 400;
  }

  // .course-content__tags

  &__tags {
    margin-bottom: 30px;
  }

  &__btns {
    margin-top: 50px;
  }
}

.tags-course {

  // .tags-course__list

  &__list {
    display: flex;
    flex-wrap: wrap;

    *:not(last-child) {
      margin-right: 10px;
    }
  }

  // .tags-course__item

  &__item {
    background-color: var(--grey-color-alt);
    padding: 2px 8px;
    border-radius: 5px;
    white-space: pre-wrap;
    color: rgb(51, 51, 51);
    font-size: .9rem;
    letter-spacing: 1.1px;
    font-weight: 500;
    margin-bottom: 10px;
    cursor: grab;
    transition: all .3s ease-in-out;

    @include hover {
      background-color: var(--blue-light-color-05);
    }

    &--active {
      background-color: var(--pink-color);
    }
  }
}


.about-course {
  padding-top: 50px;
  padding-bottom: 50px;
  // .about-course__tabs

  &__tabs {}

  // .about-course__nav

  &__nav {
    width: 100%;
    display: flex;

    @include m-768 {
      overflow-x: auto;
      padding-bottom: 20px;

      * {
        margin-right: 14px;
      }
    }
  }

  // .about-course__btn

  &__btn {
    font-size: 1.2rem;
    font-weight: 700;
    padding: 18px 26px 20px;
    text-align: center;
    letter-spacing: .1rem;
    background-color: var(--grey-color);
    border: 3px solid var(--grey-color);
    border-radius: 24px 24px 0 0;
    border-bottom: none;
    white-space: nowrap;
    outline: none;
    transition: background-color .1s ease-in-out, border-color .1s ease-in-out;

    @include hover {
      background-color: var(--grey-color-alt);
      border: 3px solid var(--grey-color-alt);
      border-bottom: none;
    }

    @include m-650 {
      font-size: 1.1rem;
    }

    &.active {
      pointer-events: none;
      background-color: var(--light-color);
      border-color: var(--dark-color);
      z-index: 2;
      border-bottom: none;
    }

    @include m-768 {
      border: 3px solid var(--grey-color);
      border-radius: 24px;
      padding: 18px;

      @include hover {
        border: 3px solid var(--grey-color-alt);
      }

      &.active {
        border: 3px solid var(--dark-color);
      }
    }
  }

  // .about-course__content

  &__content {
    border-radius: 0 0 24px 24px;
    position: relative;
    z-index: 1;
    margin-top: -3px;
    overflow: hidden;
    background-color: var(--light-color);
    border: 3px solid var(--dark-color);
    min-height: 300px;

    @include m-768 {
      border-radius: 24px;
    }
  }

  // .about-course__item

  &__item {
    padding: 60px 40px 40px;

    @include m-460 {
      padding: 60px 12px 40px;
    }

    &--list {
      ul {
        display: grid;
        gap: 1rem;
        grid-template-columns: 1fr 1fr 1fr;

        @include m-960 {
          grid-template-columns: 1fr 1fr;
        }

        @include m-650 {
          grid-template-columns: 1fr;
        }

        li {
          background-color: var(--grey-color-alt);
          border: 3px solid var(--light-color);
          padding: 2rem;
          border-radius: 14px;
          display: flex;
          justify-content: center;
          flex-direction: column;
          transition: all .3s ease-in-out;
          position: relative;

          &::before {
            content: '';
            position: absolute;
            width: 100px;
            height: 100px;
            right: -40px;
            top: -30px;
            background: url('../img/svg/icon.svg') top / contain no-repeat;
            transform: scale(0);
            transition: transform .3s ease-in-out;
          }

          @include hover {
            box-shadow: 0px 30px 80px rgba(47, 65, 88, 0.20);
            border: 3px solid var(--blue-light-color);
            background-color: var(--light-color);

            &::before {
              transform: scale(1);
            }

          }

          h3,
          h4 {
            font-size: 1.3rem;
          }
        }
      }
    }

    &--price {}
  }
}

.price-card {

  // .price-card__list

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(100px, auto);
    gap: 2rem;

    @include m-650 {
      grid-template-columns: 1fr;
    }

    &>li {
      display: grid;
      grid-template-columns: 1fr 1.7fr;
      grid-template-areas: "a b" "a c";
      border-radius: 14px;
      border: 3px solid var(--grey-dark-color);
      padding: 0 1rem 1rem;

      @include m-1070 {
        grid-template-columns: 1fr 2fr;
        grid-template-areas: "a b" "c c";
        align-items: start;
      }

      @include m-860 {
        display: block;
      }
    }
  }

  // .price-card__img

  &__img {
    padding-top: 1.3rem;
    grid-area: a;
    text-align: center;

    img {
      border-radius: 14px;

      @include m-650 {
        width: 300px;
      }
    }
  }

  // price-card__title{
  &__title {
    grid-area: b;
    padding-left: 1rem;

    h3,
    h4 {
      margin: 20px 0;
      font-size: 1.3rem;
      line-height: 120%;
    }

    @include m-860 {
      padding-left: 0;
      position: relative;
    }
  }

  // .price-card__content

  &__content {
    grid-area: c;
    padding-left: 1rem;
    padding-right: 1rem;
    justify-content: start;

    @include m-1070 {
      padding-top: 1rem;
      align-items: start;
    }

    @include m-860 {
      padding-right: 0;
      padding-left: 0;
      padding-top: 0;
    }

    h5 {
      font-weight: 400;
      font-size: 1rem;
      color: var(--grey-color-8);
    }

    ul {

      li {
        margin-bottom: 20px;
      }
    }
  }

  // price-card__desc
  &__desc {
    color: var(--light-color);
    font-weight: 400;
    font-size: .9rem;
    background-color: var(--grey-dark-color);
    padding: 6px 14px;
    margin: 0;
    border-radius: 0 0 8px 8px;
    display: inline-block;
    line-height: 120%;

    @include m-860 {
      border-radius: 8px;
      position: absolute;
      left: 50%;
      top: -100px;
      transform: translateX(-50%);
      width: auto;
      text-align: center;
    }

    @include m-650 {
      font-size: 1rem;
      top: -70px;
    }
  }
}

.course-price {
  font-size: 1.4rem;
  margin: 14px 0;
  font-weight: 700;
  color: var(--icon-color-lighten);

  span {
    font-weight: 400;
    font-size: .9rem;
    color: var(--grey-color-5);
  }
}