// =====================
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;

  &.open {
    opacity: 1;
    visibility: visible;
    transition: all .3s;
  }
}

.hamburger-slim {
  background: transparent;
  position: relative;
  height: 32px;
  width: 32px;
  z-index: 300;
  cursor: pointer;

  @media (min-width: 959.98px) {
    display: none;
  }

  &:before,
  &:after {
    background: #000;
    backface-visibility: hidden;
    content: "";
    height: 1px;
    left: 50%;
    transform: translate(-50%);
    transition: 0.75s;
    width: 22px;
  }

  &:before {
    box-shadow: #000 0 8px 0 0;
    position: absolute;
    top: 8px;
  }

  &:after {
    position: absolute;
    top: calc(100% - 8px);
  }

  &.active {
    background-color: #fff;
    border-radius: 50%;

    &:before {
      box-shadow: transparent 0 0 0 0;
      top: 50%;
      transform: translate(-50%) rotate(225deg);
    }

    &:after {
      top: 50%;
      transform: translate(-50%) rotate(315deg);
    }
  }
}


// Верхняя навигация ============================
.header {
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  margin: 0 auto;
  z-index: 50;
  border: none;
  outline: none;
  background: #fff;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.nav-contacts__btn,
.nav-contacts__phone-header,
.navigation__nav-contacts,
.nav-contacts__socials,
.nav__list {
  align-items: center;
}

.navigation {

  // .navigation__nav-contacts

  &__nav-contacts {
    justify-content: end;

    min-height: 86px;
    padding: 16px 0;
    border: none;
    border-bottom: 1px solid var(--grey-lite-color);

    >*+* {
      margin-left: 30px;
    }

    @media (max-width: 620px) {
      >*+* {
        margin-left: 20px;
      }
    }
  }
}

.nav-contacts {
  display: flex;
  justify-content: start;
  // .nav-contacts__logo

  &__logo {
    flex: 1;
  }

  // .nav-contacts__phone-header

  &__phone-header {
    // flex: 0 0 266px;

    @media (max-width:1000px) {
      flex: 0 0 280px;
    }

    @media (max-width:740px) {
      margin-right: auto;
    }

    @media (max-width: 530px) {
      flex: 1;
      justify-content: end;
    }

    .nav__footer & {
      margin-top: 20px;
      margin-right: auto;
      justify-content: start;
    }
  }

  // .nav-contacts__btn

  &__btn {
    // margin-right: auto;


    @media (max-width:1000px) {
      flex-direction: column;
      align-items: start;
      flex: 0 0 170px;
      justify-self: start;
      margin-right: auto;


      @media (max-width: 740px) {
        display: none;
      }
    }

    li {
      display: inline-block;
      padding-left: 36px;
      position: relative;
      font-weight: 600;
      letter-spacing: 0.2px;
      color: var(--dark-color);

      a {
        font-weight: 600;
        letter-spacing: 0.2px;
        color: var(--dark-color);
      }

      &::before {
        content: '';
        position: absolute;
        width: 30px;
        height: 30px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        background-color: var(--light-color);
        border: 0.5px solid var(--grey-lite-color);
        border-radius: 50%;
        box-shadow: 0px 4px 10px rgba(3, 3, 82, 0.09);
      }

      &::after {
        content: '';
        position: absolute;
        width: 17px;
        height: 21px;
        background-image: url('../img/svg/icon-ruli.svg');
        background-size: contain;
        background-repeat: no-repeat;
        left: 7px;
        top: 50%;
        transform: translateY(-50%);
      }

      &+li {
        margin-left: 24px;

        @media (max-width:1000px) {
          margin-left: 0px;
          margin-top: 15px;
        }

      }
    }

    .nav__footer & {
      display: flex;

      li {
        &::before {
          width: 30px;
          height: 30px;
        }

        &::after {
          width: 17px;
          height: 21px;
          left: 7px;
        }

        &+li {
          margin-top: 10px;
        }

        .header-btn {
          display: inline-block;
        }
      }


    }
  }

  // .nav-contacts__socials

  &__socials {
    // flex: 0 0 136px;

    @media (max-width: 960px) {
      display: none;
    }

    .nav__footer & {
      display: flex;
      flex: 1;
      margin-top: 20px;
    }
  }

  // .nav-contacts__hamburger-slim
  &__hamburger-slim {
    flex: 0 0 32px;
    margin-right: -8px;
  }
}

.nav__footer {
  padding: 20px 30px;
  margin-top: 0;

  @media (min-width: 960.98px) {
    display: none;
  }
}

.logo {

  // .logo__header

  &__header {
    display: block;
    width: 170px;
    height: 70px;
  }
}

.phone-header {
  display: flex;
  // .phone-header__call

  &__call {
    margin-right: 8px;

    .nav__footer & {
      align-self: start;
    }
  }

  // .phone-header__tel

  &__tel {
    @media (max-width: 530px) {
      display: none;

      .nav__footer & {
        display: block;
      }
    }

    a {
      font-size: 18px;
      line-height: 23px;
      font-weight: 600;
      text-decoration: none;
      color: var(--dark-color);
      transition: color .3s ease-in;

      .nav__footer & {
        display: inline-block;
        font-size: 20px;
        padding-top: 5px;
      }

      @media(hover: hover) and (pointer: fine) {
        &:hover {
          color: var(--black-color);
        }
      }

    }

    span {
      font-weight: 400;
      font-size: 14px;
      color: var(--dark-color);
      display: inline-block;
      line-height: 140%;

      .nav__footer & {
        font-size: 16px;
        line-height: 135%;
        margin-top: 8px;
        display: block;
      }
    }
  }
}

.header-btn {
  // display: flex;
  // font-size: 14px;
  // line-height: 18px;
  // color: var(--dark-color);
  // font-weight: 500;
  // margin-top: 5px;

  .nav__footer & {
    font-size: 16px;
    line-height: 135%;
    color: var(--dark-color);
  }

  .header-btn {
    @media (max-width:1000px) {
      display: none;
    }

  }
}

.socials {
  display: flex;

  li {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }
}

.social-icon {

  width: 30px;
  height: 30px;
  background: var(--light-color);
  border: 0.5px solid var(--grey-lite-color);
  border-radius: 50%;
  box-shadow: var(--grey-shadow-09);
  display: flex;
  align-items: center;
  justify-content: center;

  .nav__footer & {
    width: 40px;
    height: 40px;
  }

  @media(hover: hover) and (pointer: fine) {
    &:hover img {
      transform: scale(1.1);
    }
  }


  .nav__footer & img {
    width: 24px;
    height: 24px;
  }

  img {
    width: 30px;
    height: 30px;
    transition: transform .2s ease-in;

    @media (max-width: 530px) {
      width: 24px;
      height: 24px;
    }
  }
}

// Меню навигация ============================

.header-menu {
  width: 100%;
  padding: 8px 0px 8px;

  @media (max-width: 960px) {
    width: 320px;
    height: 100vh;
    padding: 0;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    transform: translateX(-150%);

    &.active {
      transform: translateX(0);
      transition: .2s ease-in-out;
    }
  }


  @media (max-width: 380px) {
    width: 100%;
  }
}

.header-content {
  width: 100%;

  @media (max-width: 960px) {
    padding: 24px 0 70px;
    height: 100%;
    display: flex;
    flex-direction: column;
    // justify-content: space-between;
    transition: .7s ease-in-out;
    overflow-x: hidden;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 0;
    }
  }
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;

  a {
    color: var(--dark-color);
    text-decoration: none;
    transition: .5s;
    font-size: 17px;
    display: inline-block;
    padding: 10px 0;

    @media (max-width: 960px) {
      padding: 10px 50px 10px 30px;
    }

    @media(hover: hover) and (pointer: fine) {
      &:hover {
        transition: .5s;
      }
    }

  }

  @media (max-width: 960px) {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: start;

    &.open {
      transform: translateX(0);
      transition: all .3s;
    }
  }

  // .nav__list
  &__list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    position: relative;

    @media (max-width: 960px) {
      flex-direction: column;
      align-items: start;
    }

    // .nav__list--dropdown
    &--dropdown {
      position: absolute;
      background-color: var(--light-color);
      left: 50%;
      top: 100%;
      transform: translateX(-50%);
      min-width: 280px;
      padding: 20px 16px 30px;
      display: block;
      box-shadow: 0px 1px 4px rgba(21, 21, 21, 0.02), 0px 8px 12px rgba(21, 21, 21, 0.08);
      border-radius: 16px;
      z-index: 80;
      transition: all 0.3s;
      opacity: 0;
      visibility: hidden;

      @media (max-width: 960px) {
        position: relative;
        right: auto;
        top: 0;
        padding: 0px;
        left: 0;
        width: 100%;
        box-shadow: none;
        display: none;

        &.transformation {
          display: block;
          opacity: 1;
          visibility: visible;
          transform: none;
          background-color: #fff;
          transition: all .3s;
        }

      }
    }

  }

  // .nav__item

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 24px;

      @media (max-width: 960px) {
        margin-right: 0;
      }
    }

    @media(hover: hover) and (pointer: fine) {
      &:hover .nav__list--dropdown {
        @media (min-width: 900.98px) {
          visibility: visible;
          opacity: 1;
          transition: all 0.3s;
        }
      }
    }


    @media (max-width: 960px) {
      margin: 0;
      width: 100%;
      padding: 0;
      border-bottom: 0.5px solid #D3D3D3;
    }

    // .nav__item--drop
    &--drop {
      position: static;

      @media (max-width: 960px) {
        position: relative;
      }

      .dropdown-submenu {
        left: 0;
        top: 100%;
        transform: translateX(0);
        width: 100%;

        @media (max-width: 960px) {
          top: 0;
        }
      }
    }

    // padding: 0 30px;

  }

  // .nav__link

  &__link {
    line-height: 20px;
    color: var(--dark-color);
    padding: 0 0 10px;
    display: block;

    @media (max-width: 960px) {
      color: var(--dark-color);
      font-weight: 500;
      background-color: transparent;
      transition: background-color .4s ease-in;

      @media(hover: hover) and (pointer: fine) {
        &:hover {
          background-color: var(--grey-lite-color);
        }
      }

    }

    &.dropdown-list__link {
      font-weight: 400;
      color: var(--grey-color);

      @media(hover: hover) and (pointer: fine) {
        &:hover {
          color: var(--black-color);
          background-color: transparent;
        }
      }


      @media (max-width: 960px) {
        padding-left: 45px;
      }
    }

    &.dropdown-submenu__list-item {
      padding: 0;
    }

    // .nav__link--drop
    &--drop {
      @media (max-width: 960px) {
        position: relative;

        @media(hover: hover) and (pointer: fine) {
          &:hover {
            background-color: var(--grey-lite-color);
          }
        }

        &.active {
          background-color: var(--grey-lite-color);
        }

        &::after {
          content: "";
          width: 16px;
          height: 8.5px;
          background-image: url("../img/svg/arrow-b.svg");
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          display: block;
          margin-left: auto;
          transition: transform 0.3s;
          position: absolute;
          right: 30px;
          top: 50%;
          transform: translateY(-50%);
        }

        &.active::after {
          transform: rotate(180deg);
          transition: all .3s;
          top: calc(50% - 5px);
        }
      }
    }
  }
}


.nav__link,
.dropdown-submenu__list-item a,
.dropdown-list__link {
  @media (max-width: 960px) {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

}



.nav__link,
.dropdown-submenu__list-item a {
  @media (max-width: 960px) {
    border: none;
  }
}


.dropdown-list {
  flex-wrap: wrap;
  width: 100%;

  &__item {
    @media (max-width: 960px) {
      width: 100%;
    }

    &:not(:last-child) {
      border: none;
      border-bottom: 0.5px solid #D3D3D3;
    }

    &:last-child {
      @media (max-width: 960px) {
        border: none;
        // border-bottom: 0.5px solid #D3D3D3;
      }
    }
  }

  // .dropdown-list__link
  &__link {
    display: inline-block;
    line-height: 20px;
    color: #5D5D63;
    padding: 10px 0;
    transition: color .3s ease-in;

    @media(hover: hover) and (pointer: fine) {
      &:hover {
        color: var(--black-color);
      }
    }

  }
}

.dropdown-submenu {
  display: flex;
  align-items: initial;

  @media (max-width: 960px) {
    display: none;
  }

  // .dropdown-submenu__item
  &__item {
    flex: 1;
    min-width: 200px;
    padding: 0 20px;

    &:not(:last-child) {
      border-right: 0.5px solid #D3D3D3;

      @media (max-width: 960px) {
        border: none;
      }
    }

    &:first-child {
      padding-left: 0;

    }

    &:last-child {
      padding-right: 0;
      padding-left: 0;
    }
  }

  // .dropdown-submenu__list
  &__list {
    padding: 0;

    li a {
      display: inline-block;
      font-size: 17px;
      line-height: 120%;
      color: var(--dark-color);
      padding: 6px 0;
      transition: color .3s ease-in;

      @media(hover: hover) and (pointer: fine) {
        &:hover {
          color: var(--black-color);
        }
      }


      @media (max-width: 960px) {
        padding: 10px 20px 6px 40px;
        font-size: 17px;
        font-weight: 400;
      }
    }
  }
}

.dropdown-submenu__title {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: var(--second-color);
  text-transform: uppercase;
  padding: 20px 0 10px;

  @media (max-width: 960px) {
    padding: 26px 20px 0 40px;
  }
}


.dropdown-submenu .dropdown-submenu__item {
  padding: 0 20px 0 20px;

  &:nth-child(3n) {
    // padding: 0 0 0 20px;
  }

  @media (max-width: 960px) {
    padding: 0;

    // &:nth-child(3n) {
    //   padding: 0;
    // }
  }
}

.dropdown-submenu .dropdown-submenu__item:nth-child(1) {
  padding: 0 20px;

  @media (max-width: 960px) {
    padding: 0;
  }
}

.dropdown-submenu__banner {
  display: inline-block;
  width: 100%;
  display: block;

  img {
    width: 100%;

    @media (max-width: 960px) {
      padding: 0 0 0 10px;
    }
  }
}
