.btn {
  display: inline-block;
  text-align: center;
  background-color: transparent;
  color: var(--dark-color);
  padding: 14px 20px;
  min-width: 160px;
  border-radius: 50px;
  border: 2px solid transparent;
  transition: all .3s ease-in-out;
  font-size: 1.2rem;
  // font-size: clamp(1rem, 8vw - 1.1rem, 1.2rem);
  letter-spacing: 0.1rem;

  &--link {
    display: inline-block;
    position: relative;
    text-decoration: none;
    color: var(--light-color);
    margin: 0 var(--spacing, 0px);
    transition: margin .25s;

    svg {
      width: 180px;
      height: 90px;
      position: absolute;
      left: 50%;
      bottom: 0px;
      transform: translate(-50%, 7px) translateZ(0);
      fill: none;
      stroke: var(--stroke, var(--light-color));
      stroke-linecap: round;
      stroke-width: 1px;
      stroke-dasharray: var(--offset, 69px) 278px;
      stroke-dashoffset: 361px;
      transition: stroke .25s ease var(--stroke-delay, 0s), stroke-dasharray .35s;
    }

    &:hover {
      --spacing: 4px;
      --stroke: var(--light-color);
      --stroke-delay: .1s;
      --offset: 180px;
    }
  }

  &--link-mb {
    @include m-1070 {
      color: var(--icon-color-lighten);

      svg {
        stroke: var(--stroke, var(--icon-color-lighten));
      }

      &:hover {
        --stroke: var(--icon-color-lighten);
      }
    }
  }

  &--w {
    background-color: var(--light-color);
    border: 2px solid var(--light-color);

    @include hover {
      border: 2px solid var(--dark-color);
    }
  }

  &--mb {
    @include m-1070 {
      border: 2px solid var(--dark-color);

      @include hover {
        color: var(--light-color);
        background-color: var(--icon-color-lighten);
        border: 2px solid var(--icon-color-lighten);
      }
    }
  }

  &--comment {
    background-color: var(--dark-color);
    color: var(--light-color);
    position: relative;
    z-index: 1;

    &::before {
      content: '';
      position: absolute;
      top: -20px;
      left: 30px;
      width: 30px;
      height: 40px;
      display: block;
      background: url('../img/svg/comment.svg') top / contain no-repeat;
      z-index: -1;
      transition: background .3s ease-in-out;
    }

    @include hover {
      background-color: var(--icon-color-lighten);

      &::before {
        background: url('../img/svg/comment-blue.svg') top / contain no-repeat;
      }
    }
  }

  &--small {
    transform: scale(0.7) translateX(-40px);
    @include m-860{
      transform: scale(0.8) translateX(-24px);
    }

    @include hover {
      transform: scale(0.74) translateX(-40px);
      @include m-860 {
          transform: scale(0.84) translateX(-24px);
        }
    }
  }

  &--border {
    background-color: var(--light-color);
    color: var(--icon-color-lighten);
    border: 2px solid var(--icon-color-lighten);
    font-weight: 600;
  }

  &--arrow {
    background-color: var(--icon-color-lighten);
    color: var(--light-color);
    position: relative;
    padding-right: 50px;
    white-space: nowrap;

    &::before {
      content: '';
      position: absolute;
      right: 10px;
      top: 50%;
      width: 25px;
      height: 20px;
      display: block;
      transform: translateY(-50%);
      background: url('../img/svg/arrow.svg') top / contain no-repeat;
    }
  }
}
