:root {
  --content-width: 1352px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --minWidth: 320;
  // Ширина полотна (макета) Максимальное значение окна
  --maxWidth: 1920;

  // FONT
  --font-family: "Platform", sans-serif;
  --font-medium: 0.8em;
  --font-lg: 1.125rem;
  --font-color: rgba(100, 116, 139, 1);
  --font-dark-color: rgb(15, 23, 42, 1);

  // BG
  --bg-grid-slate: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 32 32' width='32' height='32' fill='none' stroke='rgb(15 23 42 / 0.04)'%3e%3cpath d=' M0 .5H31.5V32'/%3e%3c/svg%3e");

  --box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.1);
  --br: 24px;
  --br50: 50%;

  // COLORS
  --light-color: #fff;
  --dark-color:#101010;
  --red-color: rgb(210, 0, 0);
  --red-dark-color: rgb(176, 13, 34);
  --pink-color: rgb(254, 205, 201);
  --grey-color: rgb(227, 227, 227);
  --grey-color: rgb(246, 246, 246);
  --grey-color-alt: #e7e9ed;
  --grey-color-10: rgba(44, 58, 75, 1);
  --grey-color-8: rgba(44, 58, 75, 0.8);
  --grey-color-5:rgba(44, 58, 75, 0.5);
  --grey-dark-color: #85a2b6;
  --violet: rgb(105, 50, 160);
  --blue-color: #0270c1;
  --blue-light-color: #aac5d5;
  --blue-light-color-05: rgba(170, 197, 213, 0.5);
  --blue-light-color-2: #3C636A;
  --blue-dark-color: rgb(7, 9, 66);


  // --icon-color: rgb(2, 90, 108);
  // --icon-color-lighten: rgba(2, 90, 108, 0.25);
  --icon-color: #1b2081;
  // --icon-color-lighten: rgba(7, 9, 66, 1);
  --icon-color-lighten: #01487a;

}


